*,
::before,
::after {
	box-sizing: border-box; }

body {
	font-family: $default-font;
	font-size: 16px;
	line-height: 1.6;
	margin: 0;
	padding: 0;
	background-color: $white; }
a {
	text-decoration: none;
	display: block;
	color: $primary; }
li {
	list-style: none; }

.container {
	max-width: 1160px;
	min-width: 320px;
	padding: 0 60px;
	margin: 0 auto; }


h1, h2, h3, h4, h5, h6, td, p {
	margin: 0;
	padding: 0;
 }	// line-height: 1

.lorem {
	background-color: #ccc; }






@media (max-width: 613px) {
	.container {
		padding: 0 15px; } }
